.skills {
  padding: 0;
  width: calc(100% - 50px);

  li {
    display: block;
    position: relative;
    background-color: #888;
    color: #fff;
    margin-bottom: 5px;
    transition: width 600ms ease-in-out;
    border-radius: 10px;
    box-shadow: 1px 1px 10px gray;

    .collapsed & {
      width: 0 !important;
    }

    p {
      color: rgb(65, 61, 61);
      padding: 2px 10px;
      margin: 0;
      font-weight: bold;

    }

  }
}