.Portfolio {
  max-width: 700px;
  margin: 0 auto;

  .block {
    height: 240px;
    box-shadow: 1px 1px 5px gray;
    background: linear-gradient(45deg, rgb(127, 228, 241), rgb(163, 192, 245));

    h4 {
      text-align: center;
      margin-bottom: 10px;
    }

    a {
      display: flex;
      flex-direction: row;
      height: 80%;
      padding: 10px;

      img {
        display: block;
        width: 300px;
        height: 100%;
        box-shadow: 2px 2px 5px gray;
      }

    }


  }
}