.card {
  margin-bottom: 10px;
  background: linear-gradient(45deg, rgb(186, 232, 238), rgb(223, 239, 241));
  z-index: 0;

  h4 {
    text-align: center;
  }

  .context {
    display: flex;
    padding: 5px;
    margin-right: 10px;
    justify-content: space-around;

    img {
      width: 500px;
      padding: 2px;
      box-shadow: 2px 2px 5px gray;
      transition: transform 300ms linear;
      z-index: 2;

      &:hover {
        transform: scale(1.2, 1.2);
      }

      &:active {
        transition: transform 100ms linear;
        transform: scale(1.1, 1.1);
      }
    }

    ul {
      display: inline-block;
      padding: 10px 20px;

      li {
        font-size: 20px;
        word-wrap: normal;
        list-style-type: none;
      }
    }
  }
}